<template>
<div>
<div v-for="result in results" :key="result.id" class="card">
  <div class="card-content">
    <div class="media">
      <div class="media-left">
        <figure class="image is-128x128">
          <img :src='`${result.image}`' alt="Placeholder image">
        </figure>
      </div>
      <div class="media-content">
        <h8 id="cap"> Title : {{ result.title }}</h8>
        <p>Rank : {{ result.rank }}</p>
        <p>Weekend : {{ result.weekend}}</p>
        <p>Gross : {{ result.gross }}</p>
        <p>Weeks : {{ result.weeks }}</p>
      </div>
    </div>
    <br><br>
  </div>
</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
     data() {
        return {
            title:[],
            rank:[],
            image:[],
            weekend:[],
            gross:[],
            weeks:[],
            results:[]
        };
    },
    async created() {
            try {
             const res = await axios.get(`https://imdb-api.com/en/API/BoxOffice/k_u64flofj`)
             this.results = res.data.items
             console.log(this.results)
            }catch(err) {
              console.log(err)
            }
     }
}
</script>


